import React from 'react'

import Text from './Text'
import { images } from '../loader'

const createjs = window.createjs

class Hero extends React.Component {
  componentDidMount() {
    const loading = document.getElementById('loading')
    if (loading) {
      loading.innerHTML = ''
      loading.style.opacity = 0
    }

    const canvasBounds = this.refs.canvas.getBoundingClientRect()
    this.refs.canvas.width = canvasBounds.width
    this.refs.canvas.height = canvasBounds.height
    this.stage = new createjs.Stage(this.refs.canvas)
    createjs.stages.HERO = this.stage
    createjs.Ticker.HERO = false
    createjs.Touch.enable(this.stage);
    this.stage.enableMouseOver();

    this.width = canvasBounds.width
    this.height = canvasBounds.height
    this.ellipseR = {
      width: Math.round(0.8 * this.width / 2),
      height: Math.round(0.75 * this.height / 2)
    }
    this.loupeColor = '#3b00ad'
    this.loupeShadow = '#6208f8'
    this.loupeR = this.height / 10
    this.luopePos = { x: 100, y: 100 }
    this.goToPos = { x: 100, y: 100 }

    this.speed = 2

    this.loupe = this.makeLoupe()
    this.loupe.x = this.luopePos.x
    this.loupe.y = this.luopePos.y

    this.main = this.makeContainer(true)
    this.zoomed = this.makeContainer()
    this.zoomed.scale = 1.5
    this.zoomed.visible = false

    this.stage.addChild(this.main, this.zoomed, this.loupe)
    createjs.Ticker.HERO = true

    setTimeout(() => {
      loading && loading.parentNode && loading.parentNode.removeChild(loading)
      this.init()
    }, 1000)
  }

  init() {
    createjs.Tween.get(this.main.title)
      .to({ y: this.height / 3 }, 1000, createjs.Ease.bounceOut)
      .call(() => {
        createjs.Tween.get(this.main.sub)
          .to({ alpha: 1, y: this.main.sub.toY }, 600)
          .call(() => {
            this.zoomed.mask = this.loupe
            this.zoomed.visible = true
            createjs.Tween.get(this.loupe)
              .to({ alpha: 1 }, 500)
              .call(() => {
                createjs.Tween.get(this.goToPos)
                  .to({ x: this.main.title.x - 100, y: this.main.title.y + this.main.title.getBounds().height / 2 }, 1000, createjs.Ease.quadOut)
                  .to({ x: this.main.title.x + this.main.title.getBounds().width + 100, }, 3000)
                  .to({ x: this.main.sub.x - 100, y: this.main.sub.y + this.main.sub.getBounds().height / 2 }, 1000, createjs.Ease.quadOut)
                  .to({ x: this.main.sub.x + this.main.sub.getBounds().width + 100, }, 3000)
                  .call(() => {
                    this.renderIcons()
                    this.letUserMove()
                  })
                  .on('change', () => this.move(this.goToPos))
              })
              .on('change', () => this.move(this.goToPos))
          })
          .on('change', () => this.move(this.goToPos))
      })
      .on('change', () => this.move(this.goToPos))
  }

  ellipse = (obj) => {
    const halfWidth = this.width / 2
    const halfHeight = this.height / 2
    const a = this.ellipseR.width
    const b = this.ellipseR.height

    let x = a
    let wait = false
    const speed = 4
    let increase = -speed

    return () => {
      if (!wait) {
        if (x >= a || x <= -a || (x >= 0 && x < speed)) {
          if (Math.abs(x) < 10) { x = 0 }
          if (x > a) { x = a }
          if (x < -a) { x = -a }
          wait = true
          setTimeout(() => wait = false, 3000)
        }

        const y = (increase < 0 ? -1 : 1) * b * Math.sqrt(Math.abs(1 - x * x / (a * a))) + halfHeight
        const X = x + halfWidth
        x += increase
        if (x >= a || x <= -a) {
          increase *= -1
        }
        obj.x = X
        obj.y = y
      }
    }
  }

  letUserMove = () => {
    const ellipsePos = { x: 0, y: 0 }
    const getEllipsePos = this.ellipse(ellipsePos)
    let userMoving = false
    const userPos = { x: 0, y: 0 }
    this.stage.on('mouseover', evt => {
      userMoving = true
      userPos.x = evt.stageX
      userPos.y = evt.stageY
    })
    this.stage.on('mouseout', () => {
      userMoving = false
    })
    this.stage.on('stagemousemove', evt => {
      userMoving = true
      userPos.x = evt.stageX
      userPos.y = evt.stageY
    })


    this.interval = setInterval(() => {
      if (!userMoving) {
        getEllipsePos()
      }
      const a = this.loupe.x - (userMoving ? userPos.x : ellipsePos.x)
      const b = this.loupe.y - (userMoving ? userPos.y : ellipsePos.y)
      const cross = Math.sqrt(a * a + b * b)
      if (cross > 5) {
        const sin = a / cross
        const cos = b / cross
        const speed = userMoving ? Math.max(2, cross / 10) : 5
        this.move({
          x: this.loupe.x - sin * speed,
          y: this.loupe.y - cos * speed
        })
      } else {
        this.move(userMoving ? userPos : ellipsePos)
      }
    }, 10)
  }

  move = ({ x, y }) => {
    this.loupe.x = x
    this.loupe.y = y

    this.zoomed.x = -x * (this.zoomed.scale - 1)
    this.zoomed.y = -y * (this.zoomed.scale - 1)

    this.main.title.letters.forEach((el, i) => {
      el.detect(x, y, this.zoomed.title.letters[i])
    })
    this.main.sub.letters.forEach((el, i) => {
      el.detect(x, y, this.zoomed.sub.letters[i])
    })

    createjs.Ticker.HERO = true
  }

  makeContainer(main) {
    const container = new createjs.Container()

    container.title = new Text('Piotr Osoba', { r: 255, g: 255, b: 179 }, this.width / 15 + 'px Jura')
    container.title.x = (this.width - container.title.getBounds().width) / 2
    container.title.y = main ? -container.title.getBounds().height - 30 : this.height / 3

    container.sub = new Text('Front End Developer', { r: 255, g: 255, b: 179 }, this.width / 25 + 'px Jura')
    container.sub.x = (this.width - container.sub.getBounds().width) / 2
    container.sub.y = main ? this.height / 3 : container.title.y + container.title.getBounds().height + this.height / 13
    if (main) {
      container.sub.alpha = 0
      container.sub.toY = this.height / 3 + container.title.getBounds().height + this.height / 13
    }

    const image = new createjs.Bitmap(images.hero)
    image.scale = this.width / image.image.naturalWidth

    container.addChild(image, container.title, container.sub)
    return container
  }

  makeLoupe() {
    const loupe = new createjs.Shape()
    loupe.graphics
      .setStrokeStyle(this.height / 80)
      .beginStroke(this.loupeShadow)
      .drawCircle(0, 0, this.loupeR)
      .setStrokeStyle(this.height / 100)
      .beginStroke(this.loupeColor)
      .drawCircle(0, 0, this.loupeR)
    loupe.shadow = new createjs.Shadow(this.loupeShadow, 0, 0, 5)
    loupe.alpha = 0

    return loupe
  }

  renderIcons() {
    const halfWidth = this.width / 2
    const halfHeight = this.height / 2
    const a = this.ellipseR.width
    const b = this.ellipseR.height

    const getY = (x, multiply = 1) => multiply * b * Math.sqrt(Math.abs(1 - (x - halfWidth) * (x - halfWidth) / (a * a))) + halfHeight

    const react = new createjs.Bitmap(images.react)
    react.x = halfWidth
    react.y = getY(react.x, -1)
    react.scale = this.loupeR / 500
    react.regX = 250
    react.regY = 250
    const react2 = react.clone()
    react2.alpha = 0.05

    const redux = new createjs.Bitmap(images.redux)
    redux.x = halfWidth - a
    redux.y = getY(redux.x)
    redux.scale = this.loupeR / 500
    redux.regX = 250
    redux.regY = 250
    const redux2 = redux.clone()
    redux2.alpha = 0.05

    const creatjs = new createjs.Bitmap(images.createjs)
    creatjs.x = halfWidth + a
    creatjs.y = getY(creatjs.x)
    creatjs.scale = this.loupeR / 500
    creatjs.regX = 250
    creatjs.regY = 250
    const creatjs2 = creatjs.clone()
    creatjs2.alpha = 0.05

    const node = new createjs.Bitmap(images.node)
    node.x = halfWidth
    node.y = getY(node.x)
    node.scale = this.loupeR / 500
    node.regX = 250
    node.regY = 250
    const node2 = node.clone()
    node2.alpha = 0.05

    this.main.addChild(react2, redux2, creatjs2, node2)
    this.zoomed.addChild(react, redux, creatjs, node)
  }


  render() {
    return (
      <canvas
        ref='canvas'
        style={{ width: '100vw', height: '50vw', maxHeight: '100vh', maxWidth: '100vw' }}
      />
    )
  }
}

export default Hero