import manifest from './manifest'

const createjs = window.createjs

export const images = {

}

const handleFileLoad = evt => {
  if (evt.item.type === 'image') {
    images[evt.item.id] = evt.result
  }
}

export default (handleComplete) => {
  const loader = new createjs.LoadQueue(true)
  loader.setMaxConnections(16);
  loader.on('fileload', handleFileLoad, this)
  loader.on('complete', handleComplete, this)
  loader.on('error', () => loader.loadManifest(manifest))
  loader.loadManifest(manifest)
}