const createjs = window.createjs

class Letter extends createjs.Text {
  constructor(x, sign, color, font) {
    super(sign, font, `rgb(${color.r},${color.g},${color.b})`)
    this.x = x
    this.startColor = color
    this.clr = color
    this.burning = false
    this.centerX = this.getBounds().width / 2
    this.centerY = this.getBounds().height / 2
    this.shadow = new createjs.Shadow(`rgb(${color.r},${color.g},${color.b})`, 0, 0, 20)


    const clr = { ...this.clr }

    this.burnIn = () => {
      createjs.Tween.get(clr)
        .to({ r: 0, g: 255, b: 253 }, 300)
        .on('change', () => {
          this.color = `rgb(${clr.r},${clr.g},${clr.b})`
          this.shadow.color = `rgb(${clr.r},${clr.g},${clr.b})`
          createjs.Ticker.HERO = true
        })
    }

    this.burnOut = () => {
      createjs.Tween.get(clr)
        .to({ r: 255, g: 255, b: 179 }, 1500)
        .on('change', () => {
          this.color = `rgb(${clr.r},${clr.g},${clr.b})`
          this.shadow.color = `rgb(${clr.r},${clr.g},${clr.b})`
          createjs.Ticker.HERO = true
        })
    }

    this.detect = (x, y, secondLetter) => {
      const point = this.globalToLocal(x, y)
      if (point.x > this.centerX - 40 && point.x < this.centerX + 40 && point.y > this.centerY - 40 && point.y < this.centerY + 40) {
        this.burning = true
        this.burnIn()
        secondLetter.burnIn()
      } else if (this.burning) {
        this.burning = false
        this.burnOut()
        secondLetter.burnOut()
      }
    }
  }
}

export default class Text extends createjs.Container {
  constructor(text, color, font) {
    super()
    this.currentLetterIndex = 0
    this.letters = text.split('').reduce((red, sign, i) => {
      const x = i === 0 ? 0 : red[i - 1].x + red[i - 1].getBounds().width
      const letter = new Letter(x, sign, color, font)
      this.addChild(letter)
      return [...red, letter]
    }, [])

    const lastLetter = this.letters[this.letters.length - 1]
    this.setBounds(
      0,
      0,
      lastLetter.x + lastLetter.getBounds().width,
      lastLetter.getBounds().height
    )
  }


}
